import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login.vue'
import Register from '../components/Register.vue'
import Welcome from '../components/Welcome.vue'
import Home from '../components/Home.vue'
import Dashboard from '../components/Dashboard.vue'
import Desktop from '../components/Desktop.vue'
import Trash from '../components/Trash.vue'
import Doc from '../components/Doc.vue'
import Space from '../components/Space.vue'
import Profile from '../components/Profile.vue'
import Help from '../components/Help.vue'

Vue.use(VueRouter)

const routes = [
  // 
  { path: '*', redirect: '/' },
  // /welcome
  { path: '/', redirect: '/welcome' },
  // Welcome/welcome
  {
    path: '/welcome',
    meta: { title: '' },
    component: Welcome
  },
  // Login/login
  {
    path: '/login',
    meta: { title: '-' },
    component: Login
  },
  // Register/register
  {
    path: '/register',
    meta: { title: '-' },
    component: Register
  },
  // Profile/profile
  {
    path: '/profile',
    meta: { title: '-' },
    component: Profile
  },
  {
    path: '/profile/:id',
    meta: { title: '-' },
    component: Profile
  },
  // Home/home
  {
    path: '/home',
    component: Home,
    redirect: '/dashboard',
    // 
    children: [
      // Dashboard/dashboard
      {
        path: '/dashboard',
        meta: { title: ' - ' },
        component: Dashboard
      },
      // Desktop/desktop
      {
        path: '/desktop',
        meta: { title: ' - ' },
        component: Desktop
      },
      // Trash/trash
      {
        path: '/trash',
        meta: { title: ' - ' },
        component: Trash
      },
      // Space/space/id
      {
        path: '/space/:id',
        meta: { title: ' - ' },
        component: Space
      }
    ]
  },
  // Doc/doc
  {
    path: '/doc/:id',
    meta: { title: ' - ' },
    component: Doc
  },
  // Help/help
  {
    path: '/help',
    meta: { title: '-' },
    component: Help
  }
]

const router = new VueRouter({
  routes
})

// 
router.beforeEach((to, before, next) => {
  // to 
  // from 
  // next 
  // next()   next('/login') /login
  if (to.path === '/login') return next()
  else if (to.path === '/register') return next()
  else if (to.path === '/welcome') return next()
  // token
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) return next('/login')
  next()
})

export default router
