<template>
  <el-container>
    <!-- -->
    <el-header>
      <div class="welcomebtn" @click="goHome">
        <div id="bars"></div>
        <div id="bars"></div>
        <div id="bars"></div>
      </div>
      <div id="triangle-top-left"></div>
      <div class="navbar">
        <div class="item">
          <!--  -->
          <el-popover
            placement="bottom"
            width="260"
            trigger="hover"
            title=""
          >
            <!--  -->
            <el-scrollbar style="height:360px">
              <!--  -->
              <div v-for="item in messageList" :key="item.id">
                <!--  -->
                <span>{{ item.content }}</span>
                <!--  -->
                <div>
                  <span>{{ item.createtime }}</span>
                  <!--  -->
                  <div v-if="item.category === 2">
                    <!--  -->
                    <el-button
                      icon="el-icon-check"
                      circle
                      size="mini"
                      @click="agreeMessage(item)"
                    ></el-button>
                    <!--  -->
                    <el-button
                      icon="el-icon-close"
                      circle
                      size="mini"
                      @click="refuseMessage(item)"
                    ></el-button>
                  </div>
                  <!--  -->
                  <div v-else>
                    <!--  -->
                    <el-button
                      icon="el-icon-check"
                      circle
                      size="mini"
                      @click="readMessage(item)"
                    ></el-button>
                  </div>
                </div>
              </div>
            </el-scrollbar>
            <el-badge
              :value="messageInfo.messageNum"
              :max="99"
              :hidden="messageInfo.isHidden"
              slot="reference"
            >
              <!--  -->
              <el-button
                id="bellbtn"
                icon="el-icon-bell"
                circle
                @click="getMessage"
              ></el-button>
            </el-badge>
          </el-popover>
        </div>
        <div class="icon-dropdown">
          <el-dropdown>
            <!--  -->
            <el-button id="iconbtn" circle>
              <img
                class="icon"
                :src="'http://123.57.67.128:8000' + image"
                alt=""
              />
            </el-button>
            <!--  -->
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="gotoProfile">
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="gotoWelcome">
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="logout" divided>
                <span></span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
      <div id="triangle-top-right"></div>
    </el-header>
    <!--  -->
    <el-main>
      <h3></h3>
      <div class="content">
        <div>
          <h4># </h4>
          <span
            ></span
          >
        </div>
        <div>
          <h4># </h4>
          <span></span>
          <h5></h5>
          <span
            ></span
          >
          <h5></h5>
          <span></span>
        </div>
        <div>
          <h4># </h4>
          <span
            ></span
          >
        </div>
        <div>
          <h4># </h4>
          <span></span>
          <p></p>
          <span
            ></span
          >
        </div>
        <div>
          <h4># </h4>
          <span
            >+</span
          >
        </div>
        <div>
          <h4># </h4>
          <span></span>
          <h5></h5>
          <span
            ></span
          >
        </div>
        <div>
          <h4># </h4>
          <span></span>
          <p></p>
          <span
            ></span
          >
        </div>
      </div>
    </el-main>
    <el-footer>
      <div class="footer">
        <span></span>
        <span></span>
      </div>
    </el-footer>
  </el-container>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      // ------------------------------------------------------------
      image: '',
      // 
      messageInfo: {
        messageNum: '',
        isHidden: false
      },
      // 
      messageList: []
    }
  },
  created() {
    // 
    // 
    this.getMessage()
    this.getImage()
  },
  methods: {
    // ---------------------------------------------------------------
    // 
    async getImage() {
      var token = window.sessionStorage.getItem('token')
      const { data: res } = await this.$http.get('/user/?token=' + token)
      this.image = res.image
    },
    // 
    async readMessage(message) {
      var token = window.sessionStorage.getItem('token')
      var id = message.id
      var patchform = {
        status: 0
      }
      const { data: res } = await this.$http.patch(
        '/message/' + id + '/?token=' + token,
        patchform
      )
      if (res === '') {
        this.$message.success('')
        this.getMessage()
      } else {
        this.$message.error(res)
      }
    },
    // 
    async agreeMessage(message) {
      var group = message.group
      var token = window.sessionStorage.getItem('token')
      var putform = {
        user_id: message.senduser,
        decision: true
      }
      const { data: res } = await this.$http.put(
        '/group/' + group + '/?token=' + token,
        putform
      )
      if (res === '') {
        this.$message.success('')
        this.readMessage(message)
      } else {
        this.$message.err(res)
      }
    },
    // 
    async refuseMessage(message) {
      var group = message.group
      var token = window.sessionStorage.getItem('token')
      var putform = {
        user_id: message.senduser,
        decision: false
      }
      const { data: res } = await this.$http.put(
        '/group/' + group + '/?token=' + token,
        putform
      )
      if (res === '') {
        this.$message.success('')
        this.readMessage(message)
      } else {
        this.$message.err(res)
      }
    },
    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async getMessage() {
      this.messageList = []
      this.messageInfo.messageNum = ''
      var token = window.sessionStorage.getItem('token')
      const { data: res } = await this.$http.get('/message/?token=' + token)
      this.messageInfo.messageNum = res[0].count
      this.messageList = res
    },
    // 
    gotoProfile() {
      this.$router.push('/profile')
    },
    // 
    gotoWelcome() {
      this.$router.push('/')
    },
    goHome() {
      this.$router.push('/home')
    },
    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    logout() {
      // token
      window.sessionStorage.removeItem('token')
      // /
      this.$router.push('/')
    }
  }
}
</script>
