<template>
  <el-container>
    <!--  -->
    <el-header>
      <!--  -->
      <h3>{{ teamInfo.teamName }}</h3>
      <!--  -->
      <div>
        <el-button class="spaceinfo" @click="teamInfoDialogVisible = true"
          ></el-button
        >
      </div>
      <!--  -->
      <div v-if="teamInfo.isManager">
        <el-button class="manage" @click="manageTeamDialogVisible = true"
          ></el-button
        >
      </div>
      <!--  -->
      <div v-if="!teamInfo.isManager">
        <el-button class="manage" @click="quitTeamDialogVisible = true"
          ></el-button
        >
      </div>
      <div class="switchbtns">
        <!--  -->
        <el-button-group>
          <el-tooltip content="" placement="bottom" effect="light">
            <el-button
              icon="el-icon-menu"
              @click="isListShow = false"
            ></el-button>
          </el-tooltip>
          <el-tooltip content="" placement="bottom" effect="light">
            <el-button
              icon="el-icon-s-unfold"
              @click="isListShow = true"
            ></el-button>
          </el-tooltip>
        </el-button-group>
      </div>
    </el-header>
    <!--  -->
    <div class="doclist" v-if="isListShow && this.docListInfo.total > 0">
      <!--  -->
      <el-table
        :data="docList"
        :default-sort="{ prop: 'title', order: 'ascending' }"
        max-height="600px"
      >
        <!--  -->
        <el-table-column
          prop="title"
          label=""
          sortable
          width="180"
          align="left"
          header-align="left"
        >
          <template slot-scope="scope">
            <!--  -->
            <el-button
              class="docname"
              @click.native="openDoc(scope.row)"
              type="text"
              circle
              >{{ scope.row.title }}</el-button
            >
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column
          prop="nickname"
          label=""
          width="160"
          align="center"
          header-align="center"
        ></el-table-column>
        <!--  -->
        <el-table-column
          prop="createtime"
          label=""
          sortable
          width="200"
          align="center"
          header-align="center"
        ></el-table-column>
        <!--  -->
        <el-table-column
          prop="updatetime"
          label=""
          sortable
          width="200"
          align="center"
          header-align="right"
        >
        </el-table-column>
        <!--  -->
        <el-table-column
          prop="lasteditor_nickname"
          label=""
          width="200"
          align="center"
          header-align="center"
        >
        </el-table-column>
        <!--  -->
        <el-table-column>
          <template slot-scope="scope">
            <!--  -->
            <el-button
              class="favbtn"
              @click.native="changeFavState(scope.row)"
              size="mini"
              :icon="
                scope.row.isFavorite ? 'el-icon-star-on' : 'el-icon-star-off'
              "
              circle
            ></el-button>
            <!--  -->
            <el-dropdown>
              <!--  -->
              <el-button
                class="morebtn"
                size="mini"
                icon="el-icon-more"
                circle
              ></el-button>
              <el-dropdown-menu class="dropdown-menu" slot="dropdown">
                <el-dropdown-item @click.native="shareDoc(scope.row)">
                  <span></span>
                </el-dropdown-item>
                <el-dropdown-item
                  v-if="scope.row.hasAuth"
                  @click.native="showSetAuthDialog(scope.row)"
                >
                  <span></span>
                </el-dropdown-item>
                <el-dropdown-item @click.native="changeFavState(scope.row)">
                  <span>{{ scope.row.isFavorite ? '' : '' }}</span>
                </el-dropdown-item>
                <el-dropdown-item
                  @click.native="showrenameDocDialog(scope.row.id)"
                >
                  <span></span>
                </el-dropdown-item>
                <el-dropdown-item @click.native="deleteDoc(scope.row)" divided>
                  <span></span>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <div class="docblock" v-if="!isListShow && this.docListInfo.total > 0">
      <div v-for="item in docList" :key="item.id">
        <!--  -->
        <!--class='template'-->
        <div class="block" @click="openDoc(item)">
          <!--  -->
          <div>
            <img class="img-doc" src="../assets/img/doc.png" alt="" />
          </div>
          <div class="menu">
            <!--  -->
            <span id="title">{{ item.title }}</span>
            <!--  -->
            <div class="info">
              <div>
                <span></span>
                <span>{{ item.updatetime }}</span>
              </div>
              <div>
                <span></span>
                <span>{{ item.lasteditor_nickname }}</span>
                <span></span>
              </div>
            </div>
          </div>
          <!--  -->
          <el-dropdown>
            <!--  -->
            <el-button
              class="morebtn"
              icon="el-icon-more"
              style="transform: rotate(90deg);"
            ></el-button>
            <!--  -->
            <el-dropdown-menu class="dropdown-menu" slot="dropdown">
              <el-dropdown-item @click.native="shareDoc(item)">
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item
                v-if="item.hasAuth"
                @click.native="showSetAuthDialog(item)"
              >
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="changeFavState(item)">
                <span>{{ item.isFavorite ? '' : '' }}</span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="showrenameDocDialog(item.id)">
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="deleteDoc(item)" divided>
                <span></span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <!--class='template'-->
      </div>
    </div>
    <!--  -->
    <el-dialog
      class="infolog"
      title=""
      :visible.sync="teamInfoDialogVisible"
      width="800px"
      :append-to-body="true"
    >
      <!--  -->
      <div>
        <span>{{ teamInfo.teamCreator }}</span>
      </div>
      <!--  -->
      <div class="membership">
        <el-table :data="teamInfo.memberList" max-height="350">
          <el-table-column
            prop="nickname"
            label=""
            sortable
            width="200"
            align="center"
            header-align="center"
          ></el-table-column>
          <el-table-column
            prop="username"
            label=""
            sortable
            width="200"
            align="center"
            header-align="center"
          ></el-table-column>
          <el-table-column
            prop="email"
            label=""
            sortable
            align="center"
            header-align="center"
          ></el-table-column>
        </el-table>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      class="manalog"
      title=""
      :visible.sync="manageTeamDialogVisible"
      width="1000px"
      :append-to-body="true"
    >
      <div class="manalog-inner">
        <!--  -->
        <el-button class="disband" @click="dissolveTeamDialogVisible = true"
          ></el-button
        >
        <!--  -->
        <div class="membership">
          <el-table
            :data="teamInfo.memberList"
            max-height="350"
            :default-sort="{ prop: 'nickname', order: 'ascending' }"
          >
            <el-table-column
              prop="nickname"
              label=""
              sortable
              width="200"
              align="center"
              header-align="center"
            ></el-table-column>
            <el-table-column
              prop="username"
              label=""
              sortable
              width="200"
              align="center"
              header-align="center"
            ></el-table-column>
            <el-table-column
              prop="email"
              label=""
              sortable
              width="350"
              align="center"
              header-align="center"
            ></el-table-column>
            <el-table-column>
              <template slot-scope="scope">
                <el-button
                  class="removebtn"
                  @click="removeMember(scope.row)"
                  size="mini"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      class="disbandlog"
      title=""
      :visible.sync="quitTeamDialogVisible"
      width="500px"
      center
      :append-to-body="true"
    >
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="quitTeamDialogVisible = false"> </el-button>
        <el-button type="primary" @click="quitTeam"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      class="disbandlog"
      title=""
      :visible.sync="dissolveTeamDialogVisible"
      width="500px"
      center
      :append-to-body="true"
    >
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dissolveTeamDialogVisible = false"> </el-button>
        <el-button type="primary" @click="dissolveTeam"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      class="defaultlog"
      title=""
      :visible.sync="shareDocDialogVisible"
      width="500px"
      @close="shareDocDialogClosed"
      :append-to-body="true"
    >
      <!--  -->
      <el-input v-model="shareDocForm.url" placeholder=""></el-input>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setAuthDialogVisible"
      width="500px"
      @close="setAuthDialogClosed"
      :append-to-body="true"
    >
      <el-select v-model="docAuth.label" placeholder="">
        <el-option
          v-for="item in authList"
          :key="item.auth"
          :label="item.label"
          :value="item.value"
        >
          <span style="float: left">{{ item.label }}</span>
          <span style="float: right; color: #8492a6; font-size: 13px">{{
            item.value
          }}</span>
        </el-option>
      </el-select>
      <!--  -->
      <el-button @click="setAuth"> </el-button>
    </el-dialog>
    <!--  -->
    <el-dialog
      class="defaultlog"
      title=""
      :visible.sync="renameDocDialogVisible"
      width="600px"
      @close="renameDocDialogClosed"
      :append-to-body="true"
    >
      <!--  -->
      <el-form :model="renameDocForm" ref="renameDocFormRef">
        <el-form-item prop="newName">
          <el-input
            v-model="renameDocForm.newName"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <el-button @click="renameDoc"> </el-button>
    </el-dialog>
    <!--  -->
    <div v-if="this.docListInfo.total > 0">
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="docListInfo.pageNum"
        :page-size="2"
        layout="total, prev, pager, next, jumper"
        :total="docListInfo.total"
      >
      </el-pagination>
    </div>
  </el-container>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      // ------------------------------------------------------------
      // 
      teamInfo: {
        teamId: '1',
        teamName: '123123',
        teamCreator: '',
        isManager: false,
        memberList: [
          {
            id: 1,
            nickname: 'user1',
            username: 'user1',
            email: 'user1@qq.com'
          }
        ]
      },

      // 
      docListInfo: {
        query: '',
        // 
        // :0
        // :1
        // :2
        type: 0,
        // 
        pageNum: 1,
        // 
        total: 32
      },

      // 
      docList: [
        {
          id: 1,
          isFavorite: true,
          title: '2',
          nickname: '1',
          createtime: '05-02 1012',
          updatetime: '05-02 1012',
          lasteditor_nickname: '2'
        },
        {
          id: 3,
          isFavorite: false,
          title: '4',
          nickname: '2',
          createtime: '05-02 1012',
          updatetime: '05-02 1041',
          lasteditor_nickname: '3'
        },
        {
          id: 2,
          isFavorite: false,
          title: '1',
          nickname: '3',
          createtime: '05-02 1012',
          updatetime: '05-02 1051',
          lasteditor_nickname: '4'
        },
        {
          id: 6,
          isFavorite: true,
          title: '3',
          nickname: '4',
          createtime: '05-02 1012',
          updatetime: '05-02 1001',
          lasteditor_nickname: '1'
        }
      ],

      // ---------------------------------------------------------------------

      // 
      shareDocForm: {
        url: ''
      },

      // 
      docAuth: {
        docId: 0,
        auth: 0,
        label: ''
      },

      // 
      renameDocForm: {
        userId: '',
        newName: ''
      },

      // ----------------------------------------------------------

      // 
      teamInfoDialogVisible: false,
      // 
      setAuthDialogVisible: false,
      // 
      manageTeamDialogVisible: false,
      // 
      quitTeamDialogVisible: false,

      // 
      dissolveTeamDialogVisible: false,

      // 
      shareDocDialogVisible: false,

      // 
      renameDocDialogVisible: false,

      // --------------------------------------------------------------------

      // 
      isListShow: false,

      // 
      authList: [
        {
          auth: 0,
          value: '',
          label: ''
        },
        {
          auth: 1,
          value: '',
          label: ''
        },
        {
          auth: 2,
          value: '',
          label: '/'
        },
        {
          auth: 3,
          value: '',
          label: '//'
        }
      ]
    }
  },
  created() {
    this.getTeamInfo()
    this.getTeamDoc()
  },
  // 
  watch: {
    $route(to, from) {
      this.getTeamInfo()
      this.getTeamDoc()
    }
  },
  methods: {
    // ---------------------------------------------------------------

    // 
    async getTeamInfo() {
      var token = window.sessionStorage.getItem('token')
      var teamId = this.$route.params.id
      const { data: res } = await this.$http.get(
        '/group/' + teamId + '/?token=' + token
      )
      this.teamInfo.memberList = res.member
      this.teamInfo.teamCreator = res.leader.nickname
      this.teamInfo.isManager = res.isManager
      this.teamInfo.teamName = res.name
    },

    // 
    async getTeamDoc() {
      this.docList = []
      this.docListInfo.total = 0
      var token = window.sessionStorage.getItem('token')
      var page = this.docListInfo.pageNum
      var teamId = this.$route.params.id
      const { data: res } = await this.$http.get(
        '/doc/group/' + teamId + '/?token=' + token + '&page=' + page
      )
      this.docList = res
      this.docListInfo.total = this.docList[0].total
    },

    // ---------------------------------------------------------------

    // 
    async dissolveTeam() {
      // 
      var teamId = this.$route.params.id
      var token = window.sessionStorage.getItem('token')
      const { data: res } = await this.$http.delete(
        '/group/' + teamId + '/?token=' + token
      )
      if (res === '') {
        this.$message.success('')
      } else {
        this.$message.error('')
      }
      this.$router.push('/dashboard')
    },

    // 
    async removeMember(member) {
      // 
      var teamId = this.$route.params.id
      var token = window.sessionStorage.getItem('token')
      const { data: res } = await this.$http.put(
        '/group/' + teamId + '/member/' + member.id + '/?token=' + token
      )
      if (res === '') {
        this.$message.success('')
      } else {
        this.$message.error('')
      }
      const { data: res2 } = await this.$http.get(
        '/group/' + teamId + '/?token=' + token
      )
      if (
        res2 === '' ||
        res2 === '' ||
        res2 === ''
      ) {
        this.$message.error('')
      }
      this.teamInfo.memberList = res2.member
      this.teamInfo.teamCreator = res2.leader.nickname
    },

    async quitTeam() {
      //
      var token = window.sessionStorage.getItem('token')
      var groupId = this.$route.params.id
      const { data: res } = await this.$http.delete(
        '/group/' + groupId + '/member' + '/?token=' + token
      )
      if (res === '') {
        this.$message.success('')
        this.quitTeamDialogVisible = false
        this.$router.push('/dashboard')
      } else {
        this.$message.error('')
      }
    },

    // ---------------------------------------------------------------

    // 
    async setAuth() {
      if (this.docAuth.label === '') {
        this.docAuth.auth = 0
      } else if (this.docAuth.label === '') {
        this.docAuth.auth = 1
      } else if (this.docAuth.label === '') {
        this.docAuth.auth = 2
      } else {
        this.docAuth.auth = 3
      }
      var token = window.sessionStorage.getItem('token')
      var patchform = {
        auth: this.docAuth.auth,
        noweditor: null
      }
      const { data: res } = await this.$http.patch(
        '/doc/' + this.docAuth.docId + '/?token=' + token,
        patchform
      )
      if (
        res === '' ||
        res === '' ||
        res === ''
      ) {
        this.$message.error(res)
      } else {
        this.$message.success('')
        this.setAuthDialogVisible = false
      }
      // docAuth
      // 
      // 
    },

    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async openDoc(docInfo) {
      this.$router.push('/doc/' + docInfo.id)
      // 
    },

    // 
    shareDoc(docInfo) {
      // 
      var url = window.location.href.replace('space', 'doc')
      this.shareDocForm.url = url
      this.shareDocDialogVisible = true
    },

    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async changeFavState(docInfo) {
      // 
      // 
      // 
      // 
      var token = window.sessionStorage.getItem('token')
      if (docInfo.isFavorite === true) {
        const { data: res } = await this.$http.delete(
          '/favorite/' + docInfo.id + '/?token=' + token
        )
        if (res === '') {
          this.$message.success('')
        } else {
          this.$$message.error('')
        }
      } else {
        const { data: res } = await this.$http.put(
          '/favorite/' + docInfo.id + '/?token=' + token
        )
        if (res === '') {
          this.$message.success('')
        } else {
          this.$$message.error('')
        }
      }
      docInfo.isFavorite = !docInfo.isFavorite
      var page = this.docListInfo.pageNum
      var teamId = this.$route.params.id
      const { data: res2 } = await this.$http.get(
        '/doc/group/' + teamId + '/?token=' + token + '&page=' + page
      )
      if (res2 === '') {
        this.$message.error('')
      }
      this.docList = res2
      this.docListInfo.total = this.docList[0].total
    },

    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async renameDoc(docInfo) {
      // 
      // 
      // 
      // 
      // 
      // 
      this.renameDocDialogVisible = false
      // 
      var token = window.sessionStorage.getItem('token')
      var patchform = {
        title: this.renameDocForm.newName,
        noweditor: null
      }
      const { data: res } = await this.$http.patch(
        '/doc/' + this.renameDocForm.userId + '/?token=' + token,
        patchform
      )
      if (
        res === '' ||
        res === '' ||
        res === ''
      ) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      var page = this.docListInfo.pageNum
      var teamId = this.$route.params.id
      const { data: res2 } = await this.$http.get(
        '/doc/group/' + teamId + '/?token=' + token + '&page=' + page
      )
      this.docList = res2
      this.docListInfo.total = this.docList[0].total
    },

    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async deleteDoc(docInfo) {
      //
      // 
      // 
      // 
      var token = window.sessionStorage.getItem('token')
      var patchform = {
        delete: '1',
        noweditor: null
      }
      const { data: res } = await this.$http.patch(
        '/doc/' + docInfo.id + '/?token=' + token,
        patchform
      )
      if (
        res === '' ||
        res === '' ||
        res === ''
      ) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      var page = this.docListInfo.pageNum
      var teamId = this.$route.params.id
      const { data: res2 } = await this.$http.get(
        '/doc/group/' + teamId + '/?token=' + token + '&page=' + page
      )
      this.docList = res2
      this.docListInfo.total = this.docList[0].total
    },

    // ------------------------------------------------------------

    // 
    showSetAuthDialog(docInfo) {
      // 
      // 
      this.docAuth.docId = docInfo.id
      this.setAuthDialogVisible = true
      this.docAuth.auth = docInfo.auth
      if (this.docAuth.auth === 0) {
        this.docAuth.label = ''
      } else if (this.docAuth.auth === 1) {
        this.docAuth.label = ''
      } else if (this.docAuth.auth === 2) {
        this.docAuth.label = '/'
      } else {
        this.docAuth.label = '//'
      }
    },

    // 
    showrenameDocDialog(id) {
      // 
      // 
      this.renameDocDialogVisible = true
      this.renameDocForm.userId = id
    },

    // ------------------------------------------------------------

    // 
    shareDocDialogClosed() {
      this.shareDocDialogVisible = false
      // 
      this.$refs.shareDocFormRef.resetFields()
    },

    // 
    setAuthDialogClosed() {
      this.setAuthDialogVisible = false
      // 
      this.docAuth.status = 0
      this.docAuth.label = ''
    },
    // pageSize!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    handleSizeChange(newSize) {
      this.docListInfo.pageSize = newSize
      // 
    },

    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    handleCurrentChange(newPage) {
      this.docListInfo.pageNum = newPage
      this.getTeamDoc()
    },

    // 
    renameDocDialogClosed() {
      // 
      this.$refs.renameDocFormRef.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.el-container {
  width: 1300px;
  height: 800px;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.el-header {
  width: 1300px;
  height: 100px !important;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.spaceinfo,
.manage {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 30px;
  left: 50px;
  color: rgb(150, 150, 150);
  background-color: transparent;
  border: none;
}
.manage {
  left: 200px;
}
.spaceinfo:hover,
.manage:hover {
  color: rgb(50, 50, 50);
  font-weight: 600;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.12), 0 0 6px rgba(0, 0, 0, 0.04);
  background-color: transparent;
}
.switchbtns {
  position: absolute;
  top: 15px;
  right: 300px;
}
.switchbtns .el-button {
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
  color: rgb(150, 150, 150);
  background-color: transparent;
  border: none;
}
.switchbtns .el-button:hover {
  color: rgb(50, 50, 50);
}
.switchbtns .el-button:active {
  color: rgb(50, 50, 50);
  transform: translateY(1px);
