<template>
  <el-container>
    <img class="background" src alt="background" />
    <!-- -->
    <el-header>
      <div class="homebtn" @click="goHome">
        <div id="bars"></div>
        <div id="bars"></div>
        <div id="bars"></div>
      </div>
      <div id="triangle-top-left"></div>
      <div class="navbar">
        <div class="item">
          <!--  -->
          <el-popover
            placement="bottom"
            width="260"
            trigger="hover"
            title=""
          >
            <!--  -->
            <el-scrollbar style="height:360px">
              <!--  -->
              <div v-for="item in messageList" :key="item.id">
                <!--  -->
                <span>{{ item.content }}</span>
                <!--  -->
                <div>
                  <span>{{ item.createtime }}</span>
                  <!--  -->
                  <div v-if="item.category === 2">
                    <!--  -->
                    <el-button
                      icon="el-icon-check"
                      circle
                      size="mini"
                      @click="agreeMessage(item)"
                    ></el-button>
                    <!--  -->
                    <el-button
                      icon="el-icon-close"
                      circle
                      size="mini"
                      @click="refuseMessage(item)"
                    ></el-button>
                  </div>
                  <!--  -->
                  <div v-else>
                    <!--  -->
                    <el-button
                      icon="el-icon-check"
                      circle
                      size="mini"
                      @click="readMessage(item)"
                    ></el-button>
                  </div>
                </div>
              </div>
            </el-scrollbar>
            <el-badge
              :value="messageInfo.messageNum"
              :max="99"
              :hidden="messageInfo.isHidden"
              slot="reference"
            >
              <!--  -->
              <el-button
                id="bellbtn"
                icon="el-icon-bell"
                circle
                @click="getMessage"
              ></el-button>
            </el-badge>
          </el-popover>
        </div>
        <div class="icon-dropdown">
          <el-dropdown>
            <!--  -->
            <el-button id="iconbtn" circle>
              <img
                class="icon"
                :src="'http://123.57.67.128:8000' + myImage"
                alt=""
              />
            </el-button>
            <!--  -->
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="gotoProfile">
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="gotoWelcome">
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="getHelp">
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="logout" divided>
                <span></span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
      <div id="triangle-top-right"></div>
    </el-header>
    <!--  -->
    <el-main>
      <!--  -->
      <div class="box-card">
        <!--  -->
        <div class="avatar">
          <!--  -->
          <el-upload
            v-if="isSelf"
            action="http://123.57.67.128:8000/upload/"
            :show-file-list="false"
            :on-success="uploadSuccess"
            :before-upload="beforeAvatarUpload"
            :on-error="uploadError"
            name="img"
            accept="image/jpeg,image/jpg,image/png"
          >
            <img
              :src="'http://123.57.67.128:8000' + userInfo.image"
              alt=""
            />
          </el-upload>
          <div class="avatar-remind" v-if="isSelf">
            <span></span>
            <p></p>
            <span>jpgpng</span>
          </div>
          <div v-else>
            <img
              :src="'http://123.57.67.128:8000' + userInfo.image"
              alt=""
            />
          </div>
        </div>
        <!--  -->
        <div class="divide"></div>
        <!--  -->
        <div class="info">
          <!--  -->
          <div class="nickname">
            <i class="el-icon-user"></i>
            <span></span>
            <el-input
              v-if="isSelf"
              v-model="userInfo.nickname"
              placeholder=""
              @change="changeNickname"
            ></el-input>
            <span v-else>{{ userInfo.nickname }}</span>
          </div>
          <!--  -->
          <div class="passwd">
            <i class="el-icon-lock"></i>
            <span></span>
            <span></span>
            <!--  -->
            <el-button
              v-if="isSelf"
              type="text"
              @click="changePasswordDialogVisible = true"
              ></el-button
            >
          </div>
          <!--  -->
          <div class="emailbox">
            <i class="el-icon-message"></i>
            <span></span>
            <span>{{ userInfo.email }}</span>
          </div>
          <!--  -->
          <div class="username">
            <i class="el-icon-bank-card"></i>
            <span></span>
            <span>{{ userInfo.userName }}</span>
          </div>
          <!-- id -->
          <div class="username">
            <i class="el-icon-postcard"></i>
            <span>ID</span>
            <span>{{ userInfo.userId }}</span>
          </div>
        </div>
      </div>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="changePasswordDialogVisible"
        width="500px"
        @close="changePasswordDialogClosed"
        center
      >
        <!--  -->
        <el-form
          :model="passwordForm"
          :rules="changePasswordRules"
          ref="changePasswordRef"
        >
          <!--  -->
          <el-form-item prop="oldPassword">
            <el-input
              v-model="passwordForm.oldPassword"
              placeholder=""
              type="password"
            ></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item prop="newPassword1">
            <el-input
              v-model="passwordForm.newPassword1"
              placeholder=""
              type="password"
            ></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item prop="newPassword2">
            <el-input
              v-model="passwordForm.newPassword2"
              placeholder=""
              type="password"
            ></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="changePasswordDialogVisible = false"
            ></el-button
          >
          <el-button @click="changePassword"></el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  data() {
    // 
    var checkPassword2 = (rule, value, callback) => {
      if (value !== this.passwordForm.newPassword1) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      // ---------------------------------------------------------------
      // 
      userInfo: {
        // ID
        userId: '1222113',
        nickname: '342',
        userName: 'WE',
        email: '111@qq.com',
        password: '',
        image: ''
        // 
      },
      // 
      myImage: '',

      // 
      messageInfo: {
        messageNum: '',
        isHidden: false
      },
      // 
      messageList: [],
      // --------------------------------------------------------------------
      // 
      passwordForm: {
        oldPassword: '',
        newPassword1: '',
        newPassword2: ''
      },
      // --------------------------------------------------------------------
      // 
      changePasswordDialogVisible: false,
      // --------------------------------------------------------------------
      // 
      changePasswordRules: {
        // 
        oldPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 16, message: ' 6  16 ', trigger: 'blur' }
        ],
        // 
        newPassword1: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 16, message: ' 6  16 ', trigger: 'blur' }
        ],
        // 
        newPassword2: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 16,
            message: ' 6  16 ',
            trigger: 'blur'
          },
          { validator: checkPassword2, trigger: 'blur' }
        ]
      },

      // ---------------------------------------------------------------
      isSelf: false
    }
  },
  created() {
    this.checkSelf()
    this.getMessage()
    // 
    this.getUserInfo()
  },
  methods: {
    // 
    checkSelf() {
      var website = window.location.hash
      if (website === '#/profile') {
        this.isSelf = true
      } else {
        this.isSelf = false
      }
    },
    // --------------------------------------------------------------
    // 
    async readMessage(message) {
      var token = window.sessionStorage.getItem('token')
      var id = message.id
      var patchform = {
        status: 0
      }
      const { data: res } = await this.$http.patch(
        '/message/' + id + '/?token=' + token,
        patchform
      )
      console.log(res)
      if (res === '') {
        this.$message.success('')
        this.getMessage()
      } else {
        this.$message.error(res)
      }
    },
    // 
    async agreeMessage(message) {
      var group = message.group
      var token = window.sessionStorage.getItem('token')
      var putform = {
        user_id: message.senduser,
        decision: true
      }
      const { data: res } = await this.$http.put(
        '/group/' + group + '/?token=' + token,
        putform
      )
      if (res === '') {
        this.$message.success('')
        this.readMessage(message)
      } else {
        this.$message.err('')
      }
    },
    // 
    async refuseMessage(message) {
      var group = message.group
      var token = window.sessionStorage.getItem('token')
      var putform = {
        user_id: message.senduser,
        decision: false
      }
      const { data: res } = await this.$http.put(
        '/group/' + group + '/?token=' + token,
        putform
      )
      if (res === '') {
        this.$message.success('')
        this.readMessage(message)
      } else {
        this.$message.err(res)
      }
    },
    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async getMessage() {
      this.messageList = []
      this.messageInfo.messageNum = ''
      var token = window.sessionStorage.getItem('token')
      const { data: res } = await this.$http.get('/message/?token=' + token)
      this.messageInfo.messageNum = res[0].count
      this.messageList = res
    },
    // 
    // 
    gotoProfile() {
      this.$router.push('/profile')
      this.checkSelf()
      this.getMessage()
      // 
      this.getUserInfo()
    },
    // 
    gotoWelcome() {
      this.$router.push('/')
    },
    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    getHelp() {
      this.$router.push('/help')
    },
    goHome() {
      this.$router.push('/home')
    },
    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    logout() {
      // token
      window.sessionStorage.removeItem('token')
      // /
      this.$router.push('/')
    },
    // --------------------------------------------------------------
    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async getUserInfo() {
      var token = window.sessionStorage.getItem('token')
      if (!this.isSelf) {
        var sbId = this.$route.params.id
        const { data: res } = await this.$http.get('/user/' + sbId)
        this.userInfo.userId = res.id
        this.userInfo.nickname = res.nickname
        this.userInfo.userName = res.username
        this.userInfo.email = res.email
        this.userInfo.image = res.image
        // 
        const { data: myres } = await this.$http.get('/user/?token=' + token)
        this.myImage = myres.image
      } else {
        const { data: res } = await this.$http.get('/user/?token=' + token)
        // userInfo
        this.userInfo.userId = res.id
        this.userInfo.nickname = res.nickname
        this.userInfo.userName = res.username
        this.userInfo.email = res.email
        this.oldNickname = res.nickname
        this.userInfo.password = res.password
        this.userInfo.image = res.image
        this.myImage = res.image
      }
    },

    // 
    // 
    beforeAvatarUpload(file) {
      var testmsg = /^image\/(jpeg|png|jpg)$/.test(file.type)
      // 2MB
      const isLt4M = file.size / 1024 / 1024 <= 4
      if (!testmsg) {
        this.$message.error('jpgpng')
        return
      }
      if (!isLt4M) {
        this.$message.error(' 4M')
        return
      }
      return testmsg && isLt4M
    },

    // 
    async uploadSuccess(res, file, fileList) {
      var token = window.sessionStorage.getItem('token')
      var patchform = {
        image: res.url
      }
      const { data: res3 } = await this.$http.patch(
        '/user' + '/?token=' + token,
        patchform
      )
      if (res3 === '') {
        this.$message.error('')
      } else {
        this.$message.success('')
        this.getUserInfo()
      }
    },

    // 
    uploadError(err, file) {
      this.$message.error(err)
    },

    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async changeNickname() {
      // userInfo
      var token = window.sessionStorage.getItem('token')
      console.log(this.oldNickname)
      var patchform = {
        nickname: this.userInfo.nickname
      }
      const { data: res } = await this.$http.patch(
        '/user/?token=' + token,
        patchform
      )
      if (res === '') {
        this.$message.error(res)
        this.userInfo.nickname = this.oldNickname
      } else {
        this.$message.success('')
        this.userInfo.nickname = res.nickname
      }
      // 
      // 
      // 
      // 
      // 
      // 
    },
    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async changePassword() {
      this.$refs.changePasswordRef.validate(async valid => {
        // 
        if (!valid) return
        if (this.passwordForm.oldPassword !== this.userInfo.password) {
          this.$message.error('')
          return
        } else if (
          this.passwordForm.oldPassword === this.passwordForm.newPassword1
        ) {
          this.$message.error('')
          return
        }
        var token = window.sessionStorage.getItem('token')
        var patchform = {
          password: this.passwordForm.newPassword1
        }
        const { data: res } = await this.$http.patch(
          '/user/?token=' + token,
          patchform
        )
        if (res === '') {
          this.$message.error(res)
        } else {
          this.$message.success('')
          this.changePasswordDialogVisible = false
        }
      })
    },
    // --------------------------------------------------------------
    // 
    changePasswordDialogClosed() {
      // 
      this.$refs.changePasswordRef.resetFields()
    }
    // --------------------------------------------------------------
    // 
    // 
  }
}
</script>
<style lang="less" scoped>
.el-container {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  position: absolute;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: whitesmoke;
  overflow: hidden;
}
.background {
  width: 100vw;
  opacity: 0.6;
}
.homebtn {
  width: 50px;
  height: 40px;
  position: absolute;
  top: 30px;
  left: 35px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  z-index: 99;
  transition: 0.2s ease-in;
  -webkit-transition: 0.2s ease-in;
}
#bars {
  width: 50px;
  height: 8px;
  border-radius: 100px;
