<template>
  <el-container>
    <!--  -->
    <el-header>
      <h3></h3>
      <div class="switchbtns">
        <!--  -->
        <el-button-group>
          <el-tooltip content="" placement="bottom" effect="light">
            <el-button
              icon="el-icon-menu"
              @click="isListShow = false"
            ></el-button>
          </el-tooltip>
          <el-tooltip content="" placement="bottom" effect="light">
            <el-button
              icon="el-icon-s-unfold"
              @click="isListShow = true"
            ></el-button>
          </el-tooltip>
        </el-button-group>
      </div>
    </el-header>
    <!--  -->
    <div class="doclist" v-if="isListShow && this.docListInfo.total > 0">
      <!--  -->
      <el-table
        :data="docList"
        :default-sort="{ prop: 'title', order: 'ascending' }"
        max-height="600px"
      >
        <!--  -->
        <el-table-column
          prop="title"
          label=""
          sortable
          width="180"
          align="left"
          header-align="left"
        >
          <template slot-scope="scope">
            <!--  -->
            <el-button
              class="docname"
              @click.native="openDoc(scope.row)"
              type="text"
              circle
              >{{ scope.row.title }}</el-button
            >
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column
          prop="nickname"
          label=""
          width="160"
          align="center"
          header-align="center"
        ></el-table-column>
        <!--  -->
        <el-table-column
          prop="createtime"
          label=""
          sortable
          width="200"
          align="center"
          header-align="center"
        ></el-table-column>
        <!--  -->
        <el-table-column
          prop="updatetime"
          label=""
          sortable
          width="200"
          align="center"
          header-align="right"
        >
        </el-table-column>
        <!--  -->
        <el-table-column
          prop="lasteditor_nickname"
          label=""
          width="200"
          align="center"
          header-align="center"
        >
        </el-table-column>
        <!--  -->
        <el-table-column>
          <template slot-scope="scope">
            <!--  -->
            <el-button
              class="favbtn"
              @click.native="changeFavState(scope.row)"
              size="mini"
              :icon="
                scope.row.isFavorite ? 'el-icon-star-on' : 'el-icon-star-off'
              "
              circle
            ></el-button>
            <!--  -->
            <el-dropdown>
              <!--  -->
              <el-button
                class="morebtn"
                size="mini"
                icon="el-icon-more"
                circle
              ></el-button>
              <el-dropdown-menu class="dropdown-menu" slot="dropdown">
                <el-dropdown-item @click.native="shareDoc(scope.row)">
                  <span></span>
                </el-dropdown-item>
                <el-dropdown-item
                  v-if="scope.row.hasAuth"
                  @click.native="showSetAuthDialog(scope.row)"
                >
                  <span></span>
                </el-dropdown-item>
                <el-dropdown-item @click.native="changeFavState(scope.row)">
                  <span>{{ scope.row.isFavorite ? '' : '' }}</span>
                </el-dropdown-item>
                <el-dropdown-item
                  @click.native="showrenameDocDialog(scope.row.id)"
                >
                  <span></span>
                </el-dropdown-item>
                <el-dropdown-item @click.native="deleteDoc(scope.row)" divided>
                  <span></span>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <div class="docblock" v-if="!isListShow && this.docListInfo.total > 0">
      <div v-for="item in docList" :key="item.id">
        <!--  -->
        <!--class='template'-->
        <div class="block" @click="openDoc(item)">
          <!--  -->
          <div>
            <img class="img-doc" src="../assets/img/doc.png" alt="" />
          </div>
          <div class="menu">
            <!--  -->
            <span id="title">{{ item.title }}</span>
            <!--  -->
            <div class="info">
              <div>
                <span></span>
                <span>{{ item.updatetime }}</span>
              </div>
              <div>
                <span></span>
                <span>{{ item.lasteditor_nickname }}</span>
                <span></span>
              </div>
            </div>
          </div>
          <!--  -->
          <el-dropdown>
            <!--  -->
            <el-button
              class="morebtn"
              icon="el-icon-more"
              style="transform: rotate(90deg);"
            ></el-button>
            <!--  -->
            <el-dropdown-menu class="dropdown-menu" slot="dropdown">
              <el-dropdown-item @click.native="shareDoc(item)">
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item
                v-if="item.hasAuth"
                @click.native="showSetAuthDialog(item)"
              >
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="changeFavState(item)">
                <span>{{ item.isFavorite ? '' : '' }}</span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="showrenameDocDialog(item.id)">
                <span></span>
              </el-dropdown-item>
              <el-dropdown-item @click.native="deleteDoc(item)" divided>
                <span></span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <!--class='template'-->
      </div>
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="shareDocDialogVisible"
      width="500px"
      @close="shareDocDialogClosed"
      :append-to-body="true"
    >
      <!--  -->
      <el-input v-model="shareDocForm.url" placeholder=""></el-input>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setAuthDialogVisible"
      width="500px"
      @close="setAuthDialogClosed"
      :append-to-body="true"
    >
      <el-select v-model="docAuth.label" placeholder="">
        <el-option
          v-for="item in authList"
          :key="item.auth"
          :label="item.label"
          :value="item.value"
        >
          <span style="float: left">{{ item.label }}</span>
          <span style="float: right; color: #8492a6; font-size: 13px">{{
            item.value
          }}</span>
        </el-option>
      </el-select>
      <!--  -->
      <el-button @click="setAuth"> </el-button>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="renameDocDialogVisible"
      width="600px"
      @close="renameDocDialogClosed"
      :append-to-body="true"
    >
      <!--  -->
      <el-form :model="renameDocForm" ref="renameDocFormRef">
        <el-form-item prop="newName">
          <el-input
            v-model="renameDocForm.newName"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <el-button @click="renameDoc"> </el-button>
    </el-dialog>
    <!--  -->
    <div v-if="this.docListInfo.total > 0">
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="docListInfo.pageNum"
        :page-size="12"
        layout="total, prev, pager, next, jumper"
        :total="docListInfo.total"
      >
      </el-pagination>
    </div>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      // 
      docListInfo: {
        query: '',
        // 
        // :0
        // :1
        // :2
        type: 0,
        // 
        pageNum: 1,
        // 
        total: 0
      },

      // 
      docList: [
        {
          id: 1,
          isFavorite: true,
          hasAuth: true,
          title: '2',
          nickname: '1',
          createtime: '05-02 1012',
          updatetime: '05-02 1012',
          lasteditor_nickname: '2',
          status: 0,
          auth: 0
        },
        {
          id: 3,
          isFavorite: false,
          hasAuth: true,
          title: '4',
          nickname: '2',
          createtime: '05-02 1012',
          updatetime: '05-02 1041',
          lasteditor_nickname: '3',
          status: 1,
          auth: 1
        },
        {
          id: 2,
          isFavorite: false,
          hasAuth: false,
          title: '1',
          nickname: '3',
          createtime: '05-02 1012',
          updatetime: '05-02 1051',
          lasteditor_nickname: '4',
          status: 0,
          auth: 2
        },
        {
          id: 6,
          isFavorite: true,
          hasAuth: false,
          title: '3',
          nickname: '4',
          createtime: '05-02 1012',
          updatetime: '05-02 1001',
          lasteditor_nickname: '1',
          status: 1,
          auth: 3
        }
      ],

      // ---------------------------------------------------------------------

      // 
      shareDocForm: {
        url: ''
      },

      // 
      docAuth: {
        docId: 0,
        auth: 0,
        label: ''
      },

      // 
      renameDocForm: {
        userId: '',
        newName: ''
      },

      // ----------------------------------------------------------
      // 
      setAuthDialogVisible: false,
      // 
      shareDocDialogVisible: false,

      // 
      renameDocDialogVisible: false,

      // --------------------------------------------------------------------

      // 
      isListShow: false,

      // 
      authList: [
        {
          auth: 0,
          value: '',
          label: ''
        },
        {
          auth: 1,
          value: '',
          label: ''
        },
        {
          auth: 2,
          value: '',
          label: '/'
        },
        {
          auth: 3,
          value: '',
          label: '//'
        }
      ]
    }
  },
  async created() {
    // 
    this.getDesktop()
  },
  methods: {
    // ---------------------------------------------------------------

    // 
    async getDesktop() {
      var token = window.sessionStorage.getItem('token')
      var page = this.docListInfo.pageNum
      const { data: res } = await this.$http.get(
        '/doc/user/?token=' + token + '&page=' + page
      )
      if (res === '') {
        this.docListInfo.total = 0
      } else {
        this.docList = res
        this.docListInfo.total = this.docList[0].total
      }
    },

    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async openDoc(docInfo) {
      this.$router.push('/doc/' + docInfo.id)
      // 
    },

    // 
    shareDoc(docInfo) {
      // 
      var url = window.location.href.replace('desktop', 'doc/' + docInfo.id)
      this.shareDocForm.url = url
      this.shareDocDialogVisible = true
    },
    // 
    async setAuth() {
      if (this.docAuth.label === '') {
        this.docAuth.auth = 0
      } else if (this.docAuth.label === '') {
        this.docAuth.auth = 1
      } else if (this.docAuth.label === '') {
        this.docAuth.auth = 2
      } else {
        this.docAuth.auth = 3
      }
      var token = window.sessionStorage.getItem('token')
      var patchform = {
        auth: this.docAuth.auth,
        noweditor: null
      }
      const { data: res } = await this.$http.patch(
        '/doc/' + this.docAuth.docId + '/?token=' + token,
        patchform
      )
      if (
        res === '' ||
        res === '' ||
        res === ''
      ) {
        this.$message.error(res)
      } else {
        this.$message.success('')
        this.setAuthDialogVisible = false
      }
      // docAuth
      // 
      // 
    },
    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async changeFavState(docInfo) {
      // 
      // 
      // 
      // 
      var token = window.sessionStorage.getItem('token')
      if (docInfo.isFavorite === true) {
        const { data: res } = await this.$http.delete(
          '/favorite/' + docInfo.id + '/?token=' + token
        )
        if (res === '') {
          this.$message.success('')
        } else {
          this.$$message.error('')
        }
      } else {
        const { data: res } = await this.$http.put(
          '/favorite/' + docInfo.id + '/?token=' + token
        )
        if (res === '') {
          this.$message.success('')
        } else {
          this.$$message.error('')
        }
      }
      docInfo.isFavorite = !docInfo.isFavorite
    },

    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async renameDoc(docInfo) {
      // 
      // 
      // 
      // 
      // 
      // 
      this.renameDocDialogVisible = false
      // 
      var token = window.sessionStorage.getItem('token')
      var patchform = {
        title: this.renameDocForm.newName,
        noweditor: null
      }
      const { data: res } = await this.$http.patch(
        '/doc/' + this.renameDocForm.userId + '/?token=' + token,
        patchform
      )
      if (
        res === '' ||
        res === '' ||
        res === ''
      ) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      var page = this.docListInfo.pageNum
      const { data: res2 } = await this.$http.get(
        '/doc/user/?token=' + token + '&page=' + page
      )
      this.docList = res2
      this.docListInfo.total = this.docList[0].total
    },

    // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    async deleteDoc(docInfo) {
      //
      // 
      // 
      // 
      var token = window.sessionStorage.getItem('token')
      var patchform = {
        delete: '1',
        noweditor: null
      }
      const { data: res } = await this.$http.patch(
        '/doc/' + docInfo.id + '/?token=' + token,
        patchform
      )
      if (
        res === '' ||
        res === '' ||
        res === ''
      ) {
        this.$message.error('')
      } else {
        this.$message.success('')
      }
      var page = this.docListInfo.pageNum
      const { data: res2 } = await this.$http.get(
        '/doc/user/?token=' + token + '&page=' + page
      )
      this.docList = res2
      this.docListInfo.total = this.docList[0].total
    },

    // ------------------------------------------------------------

    // 
    showSetAuthDialog(docInfo) {
      // 
      // 
      this.docAuth.docId = docInfo.id
      this.setAuthDialogVisible = true
      this.docAuth.auth = docInfo.auth
      if (this.docAuth.auth === 0) {
        this.docAuth.label = ''
      } else if (this.docAuth.auth === 1) {
        this.docAuth.label = ''
      } else if (this.docAuth.auth === 2) {
        this.docAuth.label = '/'
      } else {
        this.docAuth.label = '//'
      }
    },

    // 
    showrenameDocDialog(id) {
      // 
      // 
      this.renameDocDialogVisible = true
      this.renameDocForm.userId = id
    },

    // ------------------------------------------------------------

    // 
    shareDocDialogClosed() {
      this.shareDocDialogVisible = false
      this.$refs.shareDocFormRef.resetFields()
    },
    // 
    setAuthDialogClosed() {
      this.setAuthDialogVisible = false
      // 
      this.docAuth.status = 0
      this.docAuth.label = ''
    },

    // pageSize
    handleSizeChange(newSize) {
      this.docListInfo.pageSize = newSize
      // 
    },
    // 
    handleCurrentChange(newPage) {
      this.docListInfo.pageNum = newPage
      this.getDesktop()
      // 
    },

    // 
    renameDocDialogClosed() {
      // 
      this.$refs.renameDocFormRef.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.el-container {
  width: 1300px;
  height: 800px;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.el-header {
  width: 1300px;
  height: 100px !important;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.switchbtns {
  position: absolute;
  top: 15px;
  right: 300px;
}
.switchbtns .el-button {
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
  color: rgb(150, 150, 150);
  background-color: transparent;
  border: none;
}
.switchbtns .el-button:hover {
  color: rgb(50, 50, 50);
}
.switchbtns .el-button:active {
  color: rgb(50, 50, 50);
  transform: translateY(1px);
  -webkit-transform: translateY(1px);
}
.switchbtns .el-button:focus {
  color: rgb(50, 50, 50);
}
.doclist {
  width: 1100px;
  max-height: 630px;
  margin-bottom: 20px;
}
.docname {
  color: rgb(50, 50, 50);
  font-weight: 600;
}
/deep/ .el-table {
  border: none;
}
/deep/ .el-table__header tr,
/deep/ .el-table__header th {
  height: 50px;
  padding: 0;
}
/deep/ .el-table__body tr,
/deep/ .el-table__body td {
  height: 50px;
  padding: 0;
}
/deep/ .el-table .el-table__header-wrapper tr th {
  color: rgb(50, 50, 50);
  background-color: whitesmoke !important;
}
/deep/ .el-table .el-table__row {
  color: rgb(50, 50, 50);
  background-color: whitesmoke !important;
}
/deep/ .el-table .el-table__body tr.current-row > td {
  background-color: rgb(238, 238, 238) !important;
}
/deep/ .el-table .el-table__body tr:hover > td {
  background-color: rgb(238, 238, 238) !important;
}
/deep/ .el-table td,
/deep/ .el-table th.is-leaf {
  border-bottom: 1px solid rgb(221, 221, 221);
}
/deep/ .el-table .ascending .sort-caret.ascending {
  border-bottom-color: rgb(50, 50, 50);
}
/deep/ .el-table .descending .sort-caret.descending {
  border-top-color: rgb(50, 50, 50);
}
.favbtn,
.morebtn {
  margin-right: 10px;
  color: rgb(150, 150, 150);
  background-color: transparent;
  border: none;
}
.favbtn:hover,
.morebtn:hover {
  color: rgb(50, 50, 50);
  background-color: transparent;
}
.favbtn:active {
  color: rgb(50, 50, 50);
  background-color: transparent;
  transform: translateY(1px);
  -webkit-transform: translateY(1px);
}
.favbtn:focus,
.morebtn:focus {
  color: rgb(50, 50, 50);
  background-color: transparent;
}
.el-dropdown-menu__item:not(.is-disabled):hover {
  color: rgb(50, 50, 50);
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.12), 0 0 6px rgba(0, 0, 0, 0.04);
  background: linear-gradient(
    -20deg,
    rgb(245, 245, 245) 0%,
    rgb(235, 235, 235) 60%,
    rgb(245, 245, 245) 100%
  );
  transition: 0.2s linear;
  -webkit-transition: 0.2s linear;
}
.docblock {
  width: 1300px;
  margin-bottom: 20px;
  display: grid;
  grid-auto-flow: row dense;
  place-content: flex-start center;
  grid-template-columns: repeat(4, 300px);
  grid-template-rows: repeat(autofill, 100px);
  grid-gap: 5px;
  white-space: normal;
}
.block {
  width: 300px;
  height: 100px;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid rgb(231, 231, 231);
  background-color: white;
  transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
}
.block:hover {
  box-shadow: 0 0 6px rgb(206, 206, 206);
  cursor: pointer;
  transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
}
.block:hover #title {
  font-weight: 600;
}
.img-doc {
  height: 55px;
  margin: 0;
  margin-left: 30px;
  margin-right: 20px;
  opacity: 0.5;
  transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
}
.block:hover .img-doc {
  opacity: 1;
  transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
}
.menu {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 14px;
}
.menu .info {
  width: 100%;
  margin: 3px 0;
  color: rgb(172, 172, 172);
}
.el-dropdown .el-button {
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
  position: relative;
  right: 10px;
}
/deep/ .el-dialog .el-input__inner {
  width: 450px;
  color: rgb(85, 85, 85);
  font-size: 15px;
  border: none;
  border-radius: 1px;
  box-shadow: inset 0 0 6px rgb(224, 224, 224);
  transition: 0.1s linear;
  -webkit-transition: 0.1s linear;
}
/deep/ .el-dialog .el-input__inner:focus {
  box-shadow: inset 0 0 8px rgb(189, 189, 189);
}
.el-dialog .el-button {
  width: 100px;
  height: 40px;
  margin: 0;
  position: absolute;
  top: 84px;
  right: 20px;
  color: white;
  font-size: 16px;
  letter-spacing: 2px;
  text-indent: 2px;
  border: none;
  border-radius: 1px;
  background: linear-gradient(
    -20deg,
    rgb(50, 50, 50) 0%,
    rgb(70, 70, 70) 60%,
    rgb(50, 50, 50) 100%
  );
}
.el-dialog .el-button:hover {
  color: white;
}
.el-dialog .el-button:active {
  background: linear-gradient(
    -20deg,
    rgb(85, 85, 85) 0%,
    rgb(100, 100, 100) 60%,
    rgb(85, 85, 85) 100%
  );
  box-shadow: none;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  color: rgb(150, 150, 150);
  font-weight: 600;
}
/deep/ .el-dialog__headerbtn .el-dialog__close:hover {
  color: rgb(150, 150, 150);
  font-weight: 600;
}
/deep/ .el-dialog__headerbtn .el-dialog__close:active {
  color: rgb(50, 50, 50);
  font-weight: 600;
  transform: translateY(2px);
  -webkit-transform: translateY(2px);
}
</style>
